html {
    font-size: 30px;
}

header {
    & > section:first-child {
        section:first-child {
            button {
                width: 180px;
            }
        }
        section:last-child {
            & > aside#aside-menu:last-child {
                height: 200px;
            }
        }
    }
    & > section:last-child {
        height: 400px;
        section {
            height: 200px;
            article {
                width: 800px;
                input {
                    padding: 20px;
                    width: 900px;
                }
                svg {
                    right: 30px;
                }
            }
        }
    }
}

main {
    height: 1450px;
    padding-top: 250px;
    & section {
        article {
            height: 400px;
        }
    }
}

footer {
    position: relative;
    & * {
        font-size: 30px;
    }
    & > article {
        width: max-content;
        height: 340px;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
        & > article {
            width: 50%;
            justify-content: space-around;
        }
    }
    & > section {
        height: 200px;
        width: 98%;
        section:first-child {
            width: 380px;
            svg {
                width: 40px;
                height: 40px;
            }
        }
    }
}
